<template>
  <div class="home container">
    <div class="row">
      <div class="col py-5">
        <!-- 地圖經緯度功能 分頁功能-->
        <p>高雄YouBike2.0公共自行車租賃站即時資訊</p>
        <select v-model="areaSelected" class="mr-3">
          <option disabled value="">請選擇鄰近區域</option>
          <option v-for="(option,index) in options" :key="index">
            {{ option }}
          </option>
        </select>
        <select v-model="sortselected">
          <option disabled value="">排序方式(多到少)</option>
          <option v-for="(sort,index) in sortOptions" :value="sort.value" :key="index">
            {{ sort.txt }}
          </option>
        </select>
        <table class="table table-hover mt-3">
          <thead class="thead-light">
            <tr class="align-middle">
              <th class="align-middle text-nowrap" scope="col">租賃站名稱</th>
              <th class="align-middle text-nowrap">行政區</th>
              <th class="align-middle text-nowrap">車輛資訊</th>
              <th class="align-middle text-nowrap">地址</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in initData" :key="index" v-show="areaSelected === '' || areaSelected === item.sarea">
              <th class="align-middle" scope="row">{{ namefilter(item.sna) }}</th>
              <th class="align-middle">{{ item.sarea }}</th>
              <th class="align-middle text-nowrap">可借車輛: {{ item.sbi }}<br>可還車輛: {{ item.bemp }}<br>總車位: {{ item.tot }}</th>
              <th class="align-middle">{{ item.ar }}</th>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios'
export default {
  name: 'Rate',
  components: {
    // HelloWorld
  },
  data () {
    return {
      options: [],
      areaSelected: '',
      sortselected: '',
      initData: [],
      varietytrans: [
        { sna: '租賃站場站名稱' },
        { sarea: '行政區' },
        { ar: '地址' },
        { tot: '總車位' },
        { sbi: '可借車輛' }
      ],
      sortOptions: [
        { value: 'tot', txt: '總車位' },
        { value: 'sbi', txt: '可借車輛' },
        { value: 'bemp', txt: '可還車輛' }
      ]
    }
  },
  created () {
    this.getRate()
  },
  watch: {
    sortselected () {
      this.changeSort(this.sortselected)
    }
  },
  methods: {
    getRate () {
      console.log('rate')
      axios({
        method: 'get',
        url: 'https://api.kcg.gov.tw/api/service/Get/b4dd9c40-9027-4125-8666-06bef1756092',
        responseType: 'json'
      }).then(res => {
        this.initData = res.data.data.retVal
        for (let i = 0; i < res.data.data.retVal.length; i++) {
          this.options.push(res.data.data.retVal[i].sarea)
        }
        this.options = [...new Set(this.options)]
      })
    },
    changeSort (value) {
      this.initData = this.initData.sort((a, b) => {
        switch (value) {
          case 'tot':
            return b.tot - a.tot
          case 'sbi':
            return b.sbi - a.sbi
          case 'bemp':
            return b.bemp - a.bemp
          default:
            break
        }
      })
    },
    namefilter (value) {
      if (!value) return ''
      value = value.split('_')
      return value[1]
    }
  }
}
</script>
